@import url(https://fonts.googleapis.com/css?family=Lato:300,400,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
  width:80%;
  display: flex;
  justify-content: space-between;
}
.btn{padding:10px;border-radius:5px;text-transform:uppercase;font-size:10px;border:2px solid #65a15a;background-color:transparent;color:#65a15a;font-weight:bold;transition:background-color 200ms, border-color 200ms, color 300ms}.btn:disabled{background-color:#b7e3ae;color:#fff;cursor:auto;border-color:#b7e3ae}.btn:hover:enabled{background-color:#65a15a;color:#fff;cursor:pointer}.btn-inModal{font-size:14px;margin:0;text-transform:none}.btn-inModal__yes{background-color:#65a15a;color:#fff}.btn-inModal__yes:hover{background-color:#254b25;border-color:#254b25}.btn-delete-from-cart{padding:5px 8px}.increase-product-amount,.decrease-product-amount{padding:5px 8px;margin:0;padding-top:8px}

.modal{display:flex;justify-content:center}.modal-backdrop{background-color:rgba(0,0,0,0.6);width:100%;display:flex;top:0;left:0;bottom:0;right:0;position:fixed}.modal-main{background-color:#fff;max-width:50%;min-width:25%;max-height:50vh;border-radius:5px;z-index:1000;position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:flex;flex-direction:column}.modal-header{display:flex;justify-content:space-between;padding:10px;color:#254b25;font-size:18px}.modal-body{display:flex;border-bottom:1px solid #254b25;padding:20px 25%}.modal-footer{padding:10px;display:flex;justify-content:space-between}.close-btn:hover{color:#254b25;cursor:pointer}

.header{display:flex;padding:10px;margin:0;background-color:#fff;justify-content:center;box-shadow:0 0.125rem 0.625rem 0 rgba(0,0,0,0.08)}.header-logo{width:40px;margin-right:60px;display:flex;padding:5px;align-items:center}.header-nav{display:flex;justify-content:space-between;align-content:center;min-width:40%;flex-wrap:wrap}.header-link{text-decoration:none;text-transform:uppercase;color:#1d1d1b}.header-link:not(:last-child){margin-right:5px}.header-link.active{color:#254b25;position:relative}.header-link.active:after{content:"";display:inline-block;width:100%;border-bottom:1px solid black;position:absolute;-webkit-transform:translateX(-100%) translateY(20px);transform:translateX(-100%) translateY(20px)}.header-link:hover{color:#65a15a}.header-link:visited{color:#000}.header-link:visited:hover{color:#65a15a}.header-link:visited.active{color:#254b25}.header-link:visited.active:hover{color:#65a15a}.logo-icon{width:95%;margin-right:5px}.logo-text{width:5%;font-size:20px;text-transform:uppercase}.icon-logo{width:100%}@media screen and (max-width: 480px){.header .container{display:flex;flex-direction:column;align-items:center}.header-nav{margin-top:10px}}

.product-list{display:flex;flex-wrap:wrap;padding:20px 5px;width:100%}

.product-item{box-sizing:border-box;max-width:25%;min-width:1px;margin-bottom:10px;display:flex;flex-direction:column;border:1px solid rgba(233,233,233,0.5);border-radius:10px;padding:10px;box-shadow:-2px -2px 6px #fff,2px 2px 6px rgba(0,0,0,0.2);justify-content:space-around}.product-info{font-size:15px}.product-item:hover{box-shadow:inset -2px -2px 3px #fff,inset 2px 2px 6px rgba(0,0,0,0.2)}.product-img{width:100%;margin-top:10px}.product-title{color:#65a15a;font-size:18px;word-wrap:break-word}.product-btns{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:10px}.icon-container{width:12%;display:flex;flex-wrap:nowrap}.icon-star{width:100%;cursor:pointer;transition:fill 200ms}.icon-star:hover{fill:#254b25}.icon--checked{width:100%;cursor:pointer;fill:#65a15a}@media screen and (max-width: 768px){.product-item{max-width:50%}}@media screen and (max-width: 480px){.product-item{max-width:100%}}

.favorite-product-list{padding:50px;font-size:18px;display:flex;justify-content:center}.favourites-title{text-align:center;padding:50px}

.cart-total{display:flex;width:100%;justify-content:flex-end;align-content:flex-end;font-size:18px}.cart-total-title,.cart-total__price{margin-right:10px}

.product-inCart-item{box-sizing:border-box;max-width:100%;min-width:1px;margin-bottom:10px;display:flex;border:1px solid rgba(233,233,233,0.5);border-radius:10px;padding:10px;box-shadow:-2px -2px 6px #fff,2px 2px 6px rgba(0,0,0,0.2);align-items:center;justify-content:space-around}.product-inCart-info{font-size:15px;display:flex;justify-content:space-around;align-items:center;padding:0 5px}.product-inCart-counter{padding:0 5px;font-size:15px}.product-inCart-item:hover{box-shadow:inset -2px -2px 3px #fff,inset 2px 2px 6px rgba(0,0,0,0.2)}.product-inCart-img{width:50px;margin:0 10px}.product-inCart-title{color:#65a15a;font-size:16px;word-wrap:break-word;margin:0;font-weight:600;width:100px}.product-inCart-btns{display:flex;align-items:center;justify-content:space-around}.product-inCart-amount-control{padding:0 5px}.product-inCart-amount{padding:5px}.icon-container{width:20px;display:flex;flex-wrap:nowrap;margin-right:10px}.icon-star{width:100%;cursor:pointer;transition:fill 200ms}.icon-star:hover{fill:#254b25}.icon--checked{width:100%;cursor:pointer;fill:#65a15a}.product-inCart-amount-control{display:flex;justify-content:center;align-items:center;justify-items:center}@media screen and (max-width: 1081px){.product-inCart-info,.product-inCart-counter{text-align:center}}@media screen and (max-width: 768px){.product-inCart-info,.product-inCart-counter{font-size:14px}}@media screen and (max-width: 480px){.product-inCart-item{padding:5px}.cart-full .btn{font-size:8px;padding:5px}.cart-full .btn .icon{width:8px}.product-inCart-title{font-size:13px}.product-inCart-img{margin:0}}

.cart-product-list{flex-direction:column;width:100%}.cart-main{display:flex;justify-content:space-around;width:100%;align-items:flex-start}.cart-full{display:flex;justify-content:space-around}.already-bought-cart,.cart-empty{background-color:#f8f7f7;padding:50px;width:100%;font-size:18px}.bought-cart-title,.empty-cart-title{padding:50px;text-align:center}.bought-cart-title{color:#65a15a}@media screen and (max-width: 768px){.cart-full{flex-direction:column}}

.form-title{font-weight:400;margin-top:0}.cart-form{background-color:#f8f7f7;padding:30px;margin-top:20px;margin-left:20px}.form{display:flex;flex-direction:column}.form-element{height:auto;padding:15px;border-radius:5px;border:2px solid transparent;box-shadow:-2px -2px 6px #fff,2px 2px 6px rgba(0,0,0,0.2);margin-bottom:5px}.form-element:focus{background-color:#fefefe;outline:none}.form-element:hover{box-shadow:inset -2px -2px 3px #fff,inset 2px 2px 6px rgba(0,0,0,0.2)}.cart-form-btn{margin-top:15px}.error-container{padding:5px;color:#ff0059}@media screen and (max-width: 768px){.cart-form{padding:15px;margin-left:0;margin-top:0}.form-element{padding:5px}.form-title{font-size:20px}}

.main{display:flex;justify-content:center;margin-top:15px}

body{font-family:'Lato', sans-serif;color:#1d1d1b;position:relative}html{height:100%}#root,body{display:flex;flex-direction:column;height:100%;color:#1d1d1b}.main{flex:1 0 auto}footer{flex:0 0 auto}

.footer{display:flex;flex-direction:column;background-color:#1d1d1b;padding:10px;margin-top:50px;margin-bottom:0;align-items:center}.socials{display:flex;width:200px;justify-content:center;padding:10px;margin-bottom:5px}.icon-social{text-decoration:none;color:#fff;font-size:20px}.icon-social:hover{color:#65a15a;transition:color 300ms}.icon-social:not(:last-child){margin-right:10px}.footer-copyright{color:#fff;font-size:16px;font-weight:300;margin-bottom:10px}

